export default Skills;

const DIV = styled.div`

width: 80%;
margin:auto; 

color: ${props => (props.theme === "lightTheme" ? "#212121" : "#ffffff")};

 .heading{

  color: ${props => (props.theme === "lightTheme" ? "#212121" : "#ffffff")};

 }

 


/* *********** ************************************/

.skillsImages{
    width:40px;
    height: 40px;
    margin-right:5px;
    border-radius: 20px;

}

.skillsPara{
    color: ${props => (props.theme === "lightTheme" ? "#000000" : "#ffffff")};;
    font-size: 20px;
    font-weight: bold;
}

.skillsParentDiv{
     margin:0px 10px 20px 0px;
     display: flex;
     align-items: center;
     border: ${props => (props.theme === "lightTheme" ? "2px solid #000000" : "2px solid #ffffff")};
     padding:10px 10px;
     border-radius: 15px;
    
}




@media screen and (max-width: 588px) {
      /* Your styles for large devices go here */
    

   

     .skillsPara{
      font-size: 16px;
     }
      
    }

    @media screen and (max-width: 1000px) {
      /* Your styles for large devices go here */
      .softSkillsOthersParentDiv{
        flex-direction: column;
      }

  
      
    }

    
    @media screen and (min-width: 500px) {
      /* Your styles for large devices go here */
      .imageAndDetailParentDiv{
        width: 80%;
      }

   

      .techSkills{
        width: 80%;
      }

      .softSkillSection{
        width: 80%;
      }

  
      
    }


    @media screen and (max-width: 370px) {
      /* Your styles for large devices go here */

      
      
    }






//LOADER ANIMATION
.dot-spinner {
  --uib-size: 2.8rem;
  --uib-speed: .9s;
  --uib-color: ${props => (props.theme === "lightTheme" ? "#212121" : "#ffffff")};
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: var(--uib-size);
  width: var(--uib-size);
}

.dot-spinner__dot {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 100%;
  width: 100%;
}

.dot-spinner__dot::before {
  content: '';
  height: 20%;
  width: 20%;
  border-radius: 50%;
  background-color: var(--uib-color);
  transform: scale(0);
  opacity: 0.5;
  animation: pulse0112 calc(var(--uib-speed) * 1.111) ease-in-out infinite;
  box-shadow: 0 0 20px rgba(18, 31, 53, 0.3);
}

.dot-spinner__dot:nth-child(2) {
  transform: rotate(45deg);
}

.dot-spinner__dot:nth-child(2)::before {
  animation-delay: calc(var(--uib-speed) * -0.875);
}

.dot-spinner__dot:nth-child(3) {
  transform: rotate(90deg);
}

.dot-spinner__dot:nth-child(3)::before {
  animation-delay: calc(var(--uib-speed) * -0.75);
}

.dot-spinner__dot:nth-child(4) {
  transform: rotate(135deg);
}

.dot-spinner__dot:nth-child(4)::before {
  animation-delay: calc(var(--uib-speed) * -0.625);
}

.dot-spinner__dot:nth-child(5) {
  transform: rotate(180deg);
}

.dot-spinner__dot:nth-child(5)::before {
  animation-delay: calc(var(--uib-speed) * -0.5);
}

.dot-spinner__dot:nth-child(6) {
  transform: rotate(225deg);
}

.dot-spinner__dot:nth-child(6)::before {
  animation-delay: calc(var(--uib-speed) * -0.375);
}

.dot-spinner__dot:nth-child(7) {
  transform: rotate(270deg);
}

.dot-spinner__dot:nth-child(7)::before {
  animation-delay: calc(var(--uib-speed) * -0.25);
}

.dot-spinner__dot:nth-child(8) {
  transform: rotate(315deg);
}

.dot-spinner__dot:nth-child(8)::before {
  animation-delay: calc(var(--uib-speed) * -0.125);
}

@keyframes pulse0112 {
  0%,
  100% {
    transform: scale(0);
    opacity: 0.5;
  }

  50% {
    transform: scale(1);
    opacity: 1;
  }
}



`