import {Routes, Route} from "react-router-dom"
import Home from "./Home";
import Skills from "./Skills";
import Project from "./Project";
import About from "./About";
import Contact from "./Contact";

import style from "../Styles/Navbar.module.css"

function AllRoutes(){

    return <div  className={`${style.allR}`} >
       
        
        
        <Routes >
            <Route path="/" element={<Home />}  id="home" />
            <Route path="/skills" element={<Skills />} id="skills"/>
            <Route path="/project" element={<Project />} id="projects" />
            <Route path="/about" element={<About />} id="about" class="about section" />
            <Route path="/contact" element={<Contact />}  id="contact"/>
        </Routes>
    
        
    </div>

}

export default AllRoutes;